.c-footer {
	@include respond-to(handhelds) {
		flex-flow: wrap;
		justify-content: space-around;
		height: 750px;
		padding: 30px 10px;
	}
	@include respond-to(tablets) {
		flex-flow: wrap;
		justify-content: space-around;
		height: 700px;
		padding: 30px 25px;
	}
	@include respond-to(medium-screens) {
		flex-flow: wrap;
		justify-content: space-around;
		height: 480px;
		padding: 30px 50px;
	}

	position: relative;

	display: flex;
	justify-content: space-between;
	height: 288px;
	padding: 30px 100px;

	background-color: $black;
	&__logo {
		@include respond-to(tablets) {
			width: 100px;
		}

		width: 120px;
	}
	&__menu-item {
		@include respond-to(medium-screens) {
			width: 200px;
		}
		@include respond-to(tablets) {
			width: 150px;
			margin-bottom: 20px;
		}
		@include respond-to(handhelds) {
			width: 120px;
			margin-bottom: 20px;
		}
	}
	&__menu-subitem {
		display: block;

		color: #9d9d9e;
		font-size: $medium-font;
		line-height: 2;
		letter-spacing: $letter-spacing;
		text-decoration: none;
		&:first-child {
			color: $white;
			line-height: 2.25;
			letter-spacing: 0.05rem;
			pointer-events: none;
		}
		&:hover {
			text-decoration: underline;
		}
	}
	&__other {
		width: 260px;
	}
	.c-social {
		&-title {
			display: block;
			margin-bottom: 10px;

			color: $white;
			font-size: $medium-font;
			line-height: 2.25;
			letter-spacing: 0.05rem;
		}
		&-item {
			padding: 9px;
			border: 1px solid transparent;

			background-color: transparent;
			outline: none;

			color: $dark-grey;
			font-size: 14px;
			text-decoration: none;
			&:not(:last-child) {
				margin-right: 10px;
			}
			&:nth-child(2) {
				margin-left: -13px;
			}
			&:hover {
				border-color: $dark-grey;

				border-radius: $round-border-radius;

				cursor: pointer;
			}
		}
	}
}

.c__subscribe-box {
	margin-bottom: 40px;
	&-wrapper {
		@include respond-to(handhelds) {
			display: flex;
			justify-content: center;
		}
	}
	&-title {
		display: block;

		color: $white;
		font-size: $medium-font;
		line-height: 2.25;
		letter-spacing: 0.05rem;
	}
	&-text {
		display: block;
		margin-bottom: 15px;

		color: #9d9d9e;
		font-size: $medium-font;
		line-height: 1.25;
		letter-spacing: $letter-spacing;
	}
	&-input-wrapper {
		display: inline-block;
		width: 202px;
		height: 36px;
		margin-right: 10px;
	}
	&-input {
		width: 100%;
		height: 100%;
		padding: 10px;
		border: 1px solid transparent;

		border-radius: $smallest-border-radius;
		background-color: $white;
		outline: none;

		color: $black;
		font-weight: bold;
		font-size: $small-font;
		line-height: 1.71;
		letter-spacing: $letter-spacing;
		&:focus {
			border-color: $green;
		}
		&::placeholder {
			color: $light-black;
		}
	}
	&-submit-wrapper {
		position: relative;

		display: inline-block;
		width: 36px;
		height: 36px;

		vertical-align: bottom;
		&::before {
			content: '';

			position: absolute;
			top: 50%;
			left: 50%;

			width: 5px;
			height: 5px;
			border-top: solid $border-width $white;
			border-right: solid $border-width $white;

			transform: translate(-50%, -50%) rotate(45deg);
			transform-origin: center;
		}
	}
	&-submit {
		width: 100%;
		height: 100%;
		border: 1px solid transparent;

		border-radius: $smallest-border-radius;
		background-color: $green;
		outline: none;
		&:hover {
			border-color: $dark-green;

			background-color: $dark-green;

			cursor: pointer;
		}
	}
}
