@charset "UTF-8";
@font-face {
  font-family: "svg-icons";
  src: url("../../../../../fonts/svg-icons.eot");
  src: url("../../../../../fonts/svg-icons.?#iefix") format("embedded-opentype"), url("../../../../../fonts/svg-icons.woff") format("woff"), url("../../../../../fonts/svg-icons.ttf") format("truetype"), url("../../../../../fonts/svg-icons.#svg-icons") format("svg");
  font-weight: normal;
